.special-title {
  padding: 0;
  margin: 0;
  font-family: $font-oswald;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  position: relative;
  display: block;
  width: 100%;

  &::before {
    content: "";
    width: 100%;
    height: 1px;
    border-top: 3px solid #69b738;
    position: absolute;
    left: 0;
    top: 17px;
    z-index: 1;
  }

  &__container {
    display: block;
  }

  &__text {
    padding: 0 20px;
    position: relative;
    z-index: 5;
    display: inline-block;
    max-width: 185px;
    background-color: #ffffff;
  }
}

@media (min-width: $tablet-width) {
  .special-title {
    font-size: 36px;
    line-height: 36px;

    &::before {
      top: 23px;
    }

    &__text {
      padding: 0 41px 0;
      max-width: 523px;
      margin-left: 28px;
    }
  }
}

@media (min-width: $desktop-width) {
  .special-title {
    &__container {
      width: 1220px;
      margin: 0 auto;
      padding-inline: 66px;
    }

    &__text {
      margin: 0 0 0 -44px;
      padding: 0 41px 0;
    }
  }
}
