.radio__label {
  display: inline-block;
  padding: 7px 0 0 44px;
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  position: relative;
  min-height: 30px;
  box-sizing: border-box;
}
.radio__label:hover::before {
  border: 1px solid #bababa;
}
.radio__label::before {
  content: "";
  width: 27px;
  height: 27px;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
.radio__label::after {
  content: "";
  width: 13px;
  height: 13px;
  position: absolute;
  top: 8px;
  left: 8px;
  background: #69b738;
  border-radius: 50%;
  display: none;
}
.radio__input:checked + .radio__label::after {
  display: block;
}
.radio__input:focus-visible + .radio__label::before {
  border: 1px solid #69b738;
}

@media (min-width: 768px) {
  .radio__label {
    font-size: 20px;
    padding: 34px 0 0 85px;
  }
  .radio__label::before {
    width: 38px;
    height: 38px;
    top: 23px;
    left: 24px;
  }
  .radio__label::after {
    width: 17px;
    height: 17px;
    top: 35px;
    left: 36px;
  }
}
.checkbox__label {
  display: inline-block;
  padding: 7px 0 0 44px;
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  position: relative;
  min-height: 30px;
  box-sizing: border-box;
}
.checkbox__label:hover::before {
  border: 1px solid #bababa;
}
.checkbox__label::before {
  content: "";
  width: 27px;
  height: 27px;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #cdcdcd;
}
.checkbox__label::after {
  content: "";
  width: 13px;
  height: 13px;
  position: absolute;
  top: 8px;
  left: 8px;
  background-image: url("../img/form/tick-checkbox.svg");
  background-repeat: no-repeat;
  background-position: center center;
  display: none;
}
.checkbox__input:checked + .checkbox__label::after {
  display: block;
}
.checkbox__input:focus-visible + .checkbox__label::before {
  border: 1px solid #69b738;
}

@media (min-width: 768px) {
